.img {
    width: 100%;
    height: 10rem;
}
.logo{
    width: 4.5rem;
    height: 2.5rem;
}

.btnx{
    cursor: pointer;
}

.btnx:hover{
    filter: brightness(140%);
}

.grid-3{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-content: space-between;
    align-items: center;
}

.grid-2{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-content: space-between;
    align-items: center;
}

.flex-wide{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.vertical-center {
    position: fixed;
    width: 100vw;
    background-color: rgba(139, 139, 139, 0.3);
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000000;
    top: 0;
    left: 0;
  }